@import '../Core/Reset.scss';
@import "../Core/Variables.scss";

.about {
    display: flex;
    flex-direction: column;
    align-items: center;  
    margin: 1.6rem 2rem 1.6rem 2rem;
    color: $--color-black;

 &__aboutMe {
    font-size: 30px;
    font-weight: 600;
    margin: 3rem 0rem 3rem 0rem;
    font-family: $--font-family-Roboto;
    font-weight: 700;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: $--color-white;
    background-color: $--color-accent-purple;
    border-radius: 5px;
    padding: 5px 15px;
     }

    

 &__text {
     text-align: center;
     padding-bottom: 1.25rem;
     line-height: 30px;   
     font-family: $--font-family-openSans;
     font-weight: 600;
     
 }

}

@media all and (min-width:768px) {

    .about {

        &__aboutMe {
            display: flex;
            align-items: center;
            font-size: 25px;
            margin: 8rem 0rem 5rem 0rem;
        }

        &__container {
         display: flex;
         flex-direction: row;
         align-items: center;
         max-width: 1100px;
        }

        &__info {
            width: 400px;
            width: 50%;
            margin-top: 50px;
        }

        &__text {
            text-align: left;
        }

        &__img {
            max-width: 600px;
            width: 50%;
        }
    }
}

